.App {
  text-align: center;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  min-height: 100vh;
  color: white;
  padding: 20px;
}

.App-header {
  margin-bottom: 2rem;
}

.App-header h1 {
  font-size: 3.5rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.App-header p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  color: #e0e0e0;
}

.stats {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.stat {
  background: rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  min-width: 120px;
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: bold;
  color: #4ecdc4;
}

.stat-label {
  font-size: 0.9rem;
  color: #b0b0b0;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.App-main {
  max-width: 1200px;
  margin: 0 auto;
}

.workout-selection h2,
.workout-session h2,
.workout-history h2 {
  color: #4ecdc4;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0.5rem;
  min-width: 140px;
}

.btn.ai-generate {
  background: linear-gradient(45deg, #ff6b6b, #ff8e53);
  color: white;
  font-size: 18px;
  padding: 15px 30px;
}

.btn.ai-generate:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(255, 107, 107, 0.3);
}

.btn.start {
  background: #4ecdc4;
  color: white;
}

.btn.start:hover {
  background: #45b7aa;
  transform: translateY(-2px);
}

.btn.complete {
  background: #4CAF50;
  color: white;
  font-size: 18px;
  padding: 15px 30px;
}

.btn.complete:hover {
  background: #45a049;
  transform: translateY(-2px);
}

.btn.cancel {
  background: #ff6b6b;
  color: white;
}

.btn.cancel:hover {
  background: #ff5252;
  transform: translateY(-2px);
}

.workout-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin: 2rem 0;
}

.workout-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: transform 0.3s ease;
}

.workout-card:hover {
  transform: translateY(-5px);
}

.workout-card h3 {
  color: #4ecdc4;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.workout-card p {
  margin: 0.5rem 0;
  color: #e0e0e0;
}

.workout-card .calories {
  color: #ff6b6b;
  font-weight: bold;
  font-size: 1.1rem;
}

.workout-session {
  background: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 2rem 0;
}

.exercises {
  margin: 2rem 0;
}

.exercise {
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 10px;
  border-left: 4px solid #4ecdc4;
}

.exercise h3 {
  color: #4ecdc4;
  margin-bottom: 0.5rem;
}

.exercise .calories {
  color: #ff6b6b;
  font-weight: bold;
}

.workout-actions {
  margin-top: 2rem;
}

.workout-history {
  background: rgba(255, 255, 255, 0.1);
  padding: 2rem;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 2rem 0;
}

.history-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem;
  border-radius: 10px;
  border-left: 4px solid #4ecdc4;
}

.workout-name {
  font-weight: bold;
  color: #4ecdc4;
}

.workout-date {
  color: #b0b0b0;
  font-size: 0.9rem;
}

.workout-calories {
  color: #ff6b6b;
  font-weight: bold;
}

@media (max-width: 768px) {
  .App-header h1 {
    font-size: 2.5rem;
  }
  
  .stats {
    flex-direction: column;
    align-items: center;
  }
  
  .workout-list {
    grid-template-columns: 1fr;
  }
  
  .history-item {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
}
